<template>
  <div class="table">
    <div class="title">
      <p>仿真计算结果</p>
      <p>仿真结果文件</p>
    </div>
    <el-row :gutter="20">
      <!-- 仿真计算结果 -->
      <el-col :span="9" :offset="1"
        ><div class="grid-content bg-purple">
          <el-table :data="simulationResult" stripe style="width: 100%">
            <el-table-column prop="jobname" label="项目名称" width="220">
            </el-table-column>

            <el-table-column label="操作" width="170">
              <template slot-scope="scope">
                <el-button
                  @click="detailSimulationResultFn(scope.$index, scope.row)"
                  size="mini"
                  >详细结果</el-button
                >

                <el-button
                  type="danger"
                  size="mini"
                  @click="delSimulationResultFn(scope.$index, scope.row)"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
          <el-image-viewer
            v-if="showViewer"
            :on-close="
              () => {
                showViewer = false;
              }
            "
            :url-list="selectSimulationResult"
          /></div
      ></el-col>

      <!-- 仿真结果文件 -->
      <el-col :span="9" :offset="1"
        ><div class="grid-content bg-purple">
          <el-table :data="simulationInp" stripe style="width: 100%">
            <el-table-column prop="jobname" label="项目名称" width="220">
            </el-table-column>

            <el-table-column label="操作" width="170">
              <template slot-scope="scope">
                <el-button
                  @click="downloadInpFn(scope.$index, scope.row)"
                  size="mini"
                  >下载结果</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </div></el-col
      >
    </el-row>
  </div>
</template>

<script>
import axios from "axios";
import API_LIST from "@/APIListconfig";

export default {
  data() {
    return {
      // 接收仿真计算结果图片
      simulationResult: [],
      // 选中的仿真计算结果
      selectSimulationResult: [],
      showViewer: false,
      // 接收仿真结果文件
      simulationInp: [],
    };
  },
  created() {
    // 获取仿真计算结果图片
    this.getSimulationResultFn();
    // 获取仿真结果文件
    this.getSumulationInpFn();
  },

  methods: {
    // 获取仿真计算结果图片
    getSimulationResultFn() {
      axios.get(API_LIST.getSimulationResult).then((_d) => {
        this.simulationResult = _d.data;
      });
    },

    // 仿真计算结果详细数据
    detailSimulationResultFn(index, row) {
      var detailResult = this.simulationResult[index];
      // 为选中的结果的数组赋值，url-list只收数组
      this.selectSimulationResult[0] = detailResult.url1;
      this.selectSimulationResult[1] = detailResult.url2;
      this.selectSimulationResult[2] = detailResult.url9;
      this.selectSimulationResult[3] = detailResult.url3;
      this.selectSimulationResult[4] = detailResult.url4;
      this.selectSimulationResult[5] = detailResult.url5;
      this.selectSimulationResult[6] = detailResult.url6;
      this.selectSimulationResult[7] = detailResult.url7;
      this.selectSimulationResult[8] = detailResult.url8;

      // 预览大图
      this.showViewer = true;
    },

    // 删除仿真计算结果
    delSimulationResultFn(index, row) {
      var deleteResultObj = this.simulationResult[index];
      // 删除数据库中的数据
      axios
        .post(API_LIST.delSimulationResult, deleteResultObj)
        .then((_d) => {});
      // 删除后重新调用接口读取仿真计算结果
      this.getSimulationResultFn();
      // 仿真结果文件也要刷新
      this.getSumulationInpFn();
    },

    // 获取仿真结果文件
    getSumulationInpFn() {
      axios.get(API_LIST.getSimulationInp).then((_d) => {
        this.simulationInp = _d.data;
      });
    },

    // 下载仿真结果文件
    downloadInpFn(index, row) {
      var downloadInpObj = this.simulationInp[index];
      var downloadUrl = downloadInpObj.inp;
      window.location.href = downloadUrl;
    },
  },
  // 按钮预览大图为单独封装的组件，所以需要单独引入
  components: {
    "el-image-viewer": () =>
      import("element-ui/packages/image/src/image-viewer"),
  },
};
</script>

<style scoped>
.title p {
  font-size: 26px;
  float: left;
  margin-left: 160px;
  margin-right: 160px;
  margin-bottom: 25px;
}

/* 详细结果背景色 */
.el-image-viewer__wrapper /deep/.el-image-viewer__mask {
  background: transparent;
}

/* 表格标题居中 */
.el-table /deep/ .cell {
  text-align: center;
}
</style>
