import Vue from 'vue'
import VueRouter from 'vue-router'

// 页面
import main_page from '@/views/main_page'
import login from '@/views/login'
import register from '@/views/register'
// 后台管理系统
import management from '@/views/management'


// 组件
import instruction_page from '@/components/main_wrap/instruction_page'
import start_page from '@/components/main_wrap/start_page'
import user_page from '@/components/main_wrap/user_page'
import result_page from '@/components/main_wrap/result_page'
import stamping from '@/components/left_aside/simulation/stamping'
import springback from '@/components/left_aside/simulation/springback'

// 后台管理
import result_info from '@/components/management/result_info'
import user_info from '@/components/management/user_info'
import variable_info from '@/components/management/variable_info'




Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'main_page',
    component: main_page,
    redirect: '/login',
    children: [
      { path: '/instruction_page', component: instruction_page },
      { path: '/start_page', component: start_page },
      { path: '/user_page', component: user_page },
      { path: '/result_page', component: result_page },
      { path: '/stamping', component: stamping },
      { path: '/springback', component: springback },
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/register',
    name: 'register',
    component: register
  },
  {
    path: '/management',
    name: 'management',
    component: management,
    redirect: '/user_info',
    children: [
      { path: '/user_info', component: user_info },
      { path: '/variable_info', component: variable_info },
      { path: '/result_info', component: result_info },


    ]
  },


]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}