<template>
  <div>
    <div>
      <header_wrap></header_wrap>
    </div>
    <div class="aaa">
      <el-row>
        <el-col :span="22" :offset="1"
          ><div class="grid-content bg-purple-dark">
            <el-tag>冲压仿真结果</el-tag>
            <div class="demo-image">
              <div class="block">
                <p class="demonstration">应力云图-view1</p>
                <el-image
                  style="width: 300px; height: 200px"
                  :src="Result_Urls.url1"
                  :preview-src-list="srcList"
                ></el-image>
              </div>
              <div class="block">
                <p class="demonstration">应力云图-view2</p>
                <el-image
                  style="width: 300px; height: 200px"
                  :src="Result_Urls.url2"
                  :preview-src-list="srcList"
                ></el-image>
              </div>
              <div class="block">
                <p class="demonstration">关键点应力/时间曲线</p>
                <el-image
                  style="width: 300px; height: 200px"
                  :src="Result_Urls.url9"
                  :preview-src-list="srcList"
                ></el-image>
              </div>
              <el-button type="primary" plain @click="downloadResultFn"
                >下载结果文件</el-button
              >
              <el-button type="primary" plain @click="saveStampingResultFn"
                >保存仿真结果</el-button
              >
            </div>
          </div></el-col
        >
      </el-row>
      <!-- 回弹仿真结果 -->
      <el-row>
        <el-col :span="22" :offset="1">
          <div class="grid-content bg-purple-dark">
            <el-tag>回弹仿真结果</el-tag>
            <div class="demo-image">
              <div class="block">
                <p class="demonstration">应力云图-view1</p>
                <el-image
                  style="width: 300px; height: 200px"
                  :src="Result_Urls.url3"
                  :preview-src-list="srcList"
                ></el-image>
              </div>
              <div class="block">
                <p class="demonstration">应力云图-view2</p>
                <el-image
                  style="width: 300px; height: 200px"
                  :src="Result_Urls.url4"
                  :preview-src-list="srcList"
                ></el-image>
              </div>
              <div class="block">
                <p class="demonstration">x轴位移云图-view1</p>
                <el-image
                  style="width: 300px; height: 200px"
                  :src="Result_Urls.url5"
                  :preview-src-list="srcList"
                ></el-image>
              </div>
              <div class="block">
                <p class="demonstration">x轴位移云图-view2</p>
                <el-image
                  style="width: 300px; height: 200px"
                  :src="Result_Urls.url6"
                  :preview-src-list="srcList"
                ></el-image>
              </div>
              <div class="block">
                <p class="demonstration">y轴位移云图-view1</p>
                <el-image
                  style="width: 300px; height: 200px"
                  :src="Result_Urls.url7"
                  :preview-src-list="srcList"
                ></el-image>
              </div>
              <div class="block">
                <p class="demonstration">y轴位移云图-view2</p>
                <el-image
                  style="width: 300px; height: 200px"
                  :src="Result_Urls.url8"
                  :preview-src-list="srcList"
                ></el-image>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import header_wrap from "./header_wrap/header_wrap.vue";
import axios from "axios";
import API_LIST from "@/APIListconfig";

export default {
  name: "result_page",
  data() {
    return {
      // 项目名称
      jobname: "Job-12",
      // 图片地址
      // 冲压仿真结果图片地址
      Result_Urls: {
        url1: "",
        url2: "",
        url3: "",
        url4: "",
        url5: "",
        url6: "",
        url7: "",
        url8: "",
        url9: "",
        jobname: "",
        inp: "",
      },

      // 预览图片地址
      srcList: [],
    };
  },
  created() {
    this.getResultJobname();
  },
  methods: {
    getResultJobname() {
      // 获取结果文件名（即项目名）
      // 使用会话存储方法
      this.jobname = sessionStorage.getItem("jobname");
      // 使用路由传参方法
      // this.jobname = this.$route.query.jobname;

      // 获取计算结果图片
      this.Result_Urls.url1 =
        "http://192.168.31.168:5678/" + this.jobname + "/S-1.png";
      this.Result_Urls.url2 =
        "http://192.168.31.168:5678/" + this.jobname + "/S-2.png";
      this.Result_Urls.url3 =
        "http://192.168.31.168:5678/" + this.jobname + "/SR-1.png";
      this.Result_Urls.url4 =
        "http://192.168.31.168:5678/" + this.jobname + "/SR-2.png";
      this.Result_Urls.url5 =
        "http://192.168.31.168:5678/" + this.jobname + "/U1-1.png";
      this.Result_Urls.url6 =
        "http://192.168.31.168:5678/" + this.jobname + "/U1-2.png";
      this.Result_Urls.url7 =
        "http://192.168.31.168:5678/" + this.jobname + "/U2-1.png";
      this.Result_Urls.url8 =
        "http://192.168.31.168:5678/" + this.jobname + "/U2-2.png";
      this.Result_Urls.url9 =
        "http://192.168.31.168:5678/" + this.jobname + "/Stress-Time chart.png";
      // 保存项目名称，供后台读取
      this.Result_Urls.jobname = this.jobname;

      // 保存inp计算结果文件
      this.Result_Urls.inp =
        "http://192.168.31.168:5678/" + this.jobname + "/Job-2.inp";

      // 保存图片详细列表
      this.srcList[0] =
        "http://192.168.31.168:5678/" + this.jobname + "/S-1.png";
      this.srcList[1] =
        "http://192.168.31.168:5678/" + this.jobname + "/S-2.png";
      this.srcList[2] =
        "http://192.168.31.168:5678/" + this.jobname + "/Stress-Time chart.png";
      this.srcList[3] =
        "http://192.168.31.168:5678/" + this.jobname + "/SR-1.png";
      this.srcList[4] =
        "http://192.168.31.168:5678/" + this.jobname + "/SR-2.png";
      this.srcList[5] =
        "http://192.168.31.168:5678/" + this.jobname + "/U1-1.png";
      this.srcList[6] =
        "http://192.168.31.168:5678/" + this.jobname + "/U1-2.png";
      this.srcList[7] =
        "http://192.168.31.168:5678/" + this.jobname + "/U2-1.png";
      this.srcList[8] =
        "http://192.168.31.168:5678/" + this.jobname + "/U2-2.png";
    },

    // 保存计算结果到mongodb中
    saveStampingResultFn() {
      // 确认框
      this.$confirm("请确认仿真计算结果完整与正确, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          axios
            .post(API_LIST.saveSimulationResult, this.Result_Urls)
            .then((_d) => {
              if (_d.data.reg_code === 17) {
                this.$message({
                  type: "success",
                  message: _d.data.regInfo,
                });
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消保存",
          });
        });

      // console.log(this.Result_Urls);
    },

    // 下载inp结果文件
    downloadResultFn() {
      // 获取inp文件url
      var downloadUrl = this.Result_Urls.inp;
      // 跳转下载链接
      window.location.href = downloadUrl;
    },
  },
  components: { header_wrap },
};
</script>

<style scoped>
.el-col {
  background-color: #d3dce6;
  margin-top: 25px;
  padding: 15px 20px;
  border-radius: 20px;
  overflow: hidden;
}
/* 冲压仿真结果标签 */
.el-tag {
  position: relative;
  margin-bottom: 10px;
  font-size: 28px;
  background-color: #d3dce6;
  border: none;
  display: block;
  font-family: 楷体;
}
/* 图片大框 */
.block {
  float: left;
  margin-right: 52px;
  margin-bottom: 5px;
  margin-top: 5px;
}
.demo-image {
  overflow: hidden;
  width: 1100px;
}
/* 图片展示 */
.demo-image {
  float: left;
  margin-left: 10px;
}
/* 图片标题 */
.demonstration {
  font-size: 18px;
  margin-bottom: 10px;
  color: #606266;
}
/* 下载按钮 */
.demo-image .el-button {
  float: left;
  margin-top: 20px;
  margin-left: 230px;
  font-size: 20px;
}
</style>