<template>
  <div>
    <div>
      <header_wrap></header_wrap>
    </div>
    <div style="height: 500px" class="stepst">
      <!-- 步骤图 -->
      <el-steps direction="vertical" :active="active">
        <el-step
          title="步骤 1"
          description="阅读使用须知，熟悉操作过程，了解注意事项"
        ></el-step>
        <el-step
          title="步骤 2"
          description="冲压成形仿真预处理，提交仿真计算"
        ></el-step>
        <el-step
          title="步骤 3"
          description="导出结果文件，检查冲压成形仿真结果"
        ></el-step
        ><el-step
          title="步骤 4"
          description="回弹仿真预处理，提交仿真计算，分析回弹仿真结果"
        ></el-step>
      </el-steps>

      <!-- 右侧提示框 -->
      <div class="notice">
        <div style="display: flex; margin-top: 10px; height: 115px">
          <transition name="el-zoom-in-top">
            <div v-show="show1" class="transition-box">
              <p>
                {{ show1_text }}
              </p>
            </div>
          </transition>
        </div>
        <div style="display: flex; margin-top: 30px; height: 115px">
          <transition name="el-zoom-in-top">
            <div v-show="show2" class="transition-box">
              <p>
                {{ show2_text }}
              </p>
            </div>
          </transition>
        </div>
        <div style="display: flex; margin-top: 30px; height: 115px">
          <transition name="el-zoom-in-top">
            <div v-show="show3" class="transition-box">
              <p>
                {{ show3_text }}
              </p>
            </div>
          </transition>
        </div>
        <div style="display: flex; margin-top: 30px; height: 115px">
          <transition name="el-zoom-in-top">
            <div v-show="show4" class="transition-box">
              <p>
                {{ show4_text }}
              </p>
            </div>
          </transition>
        </div>
      </div>

      <!-- 下一步按钮 -->
      <el-button type="primary" plain @click="next" :disabled="disabled"
        >下一步</el-button
      >
    </div>
  </div>
</template>


<script>
import header_wrap from "./header_wrap/header_wrap.vue";

export default {
  name: "start_page",
  data() {
    return {
      active: 0,
      disabled: false,
      show1: false,
      show2: false,
      show3: false,
      show4: false,
      show1_text:
        " 仔细阅读使用须知，了解导入模型要求、相关变量仿真过程的预处理和仿真结果验证等工作。熟悉平台操作过程和注意事项，开始操作！",
      show2_text:
        "冲压成形仿真预处理，根据提示要求上传模型文件、完整填写输入框中要求的数据，在确认无误后提交计算并等待仿真计算完成。",
      show3_text:
        "等待仿真计算完成，查看冲压成形仿真结果，导出inp结果文件，检查工件成形是否满足要求。确认仿真结果无误后，开始回弹仿真预处理。",
      show4_text:
        "回弹仿真预处理，根据提示要求填写输入框中要求的数据，提交计算后等待计算完成提示，查看仿真结果。确认计算结果无误后保存。",
    };
  },
  methods: {
    next(active) {
      if (this.active++ == 4) {
        this.active = 0;
      }
      switch (active) {
        case 1:
          this.show1 = true;
          break;
        case 2:
          this.show2 = true;
          break;
        case 3:
          this.show3 = true;
          break;
        case 4:
          this.show4 = true;
          break;
      }

      if (this.active == 1) {
        this.show1 = true;
      }
      if (this.active == 2) {
        this.show2 = true;
      }
      if (this.active == 3) {
        this.show3 = true;
      }
      if (this.active == 4) {
        this.show4 = true;
        this.disabled = true;
      }
    },
  },
  components: { header_wrap },
};
</script>

<style scoped>
.el-steps {
  text-align: left;
  width: 80%;
  margin-left: 100px;
  margin-top: 40px;
  float: left;
}
.stepst {
  position: relative;
}
/* 下一步按钮 */
.el-button {
  position: absolute;
  font-size: 20px;
  right: 800px;
  top: 600px;
}
/* 步骤条文字 */
.stepst /deep/ .el-step__description {
  font-size: 20px;
  margin-top: 20px;
}
.stepst /deep/.el-step__title {
  font-size: 25px;
}
/* 右侧提示框 */
.notice {
  position: absolute;
  top: 10px;
  right: 100px;
}
/* 右侧提示框（大框在内联） */
/* 右侧提示框 （小框）*/
.transition-box {
  margin-bottom: 10px;
  width: 400px;
  height: 120px;
  border-radius: 4px;
  background-color: #409eff;
  text-align: center;
  color: #fff;
  padding: 40px 20px;
  box-sizing: border-box;
  margin-right: 20px;
  opacity: 0.8;
}
.transition-box p {
  margin-top: -25px;
  font-family: "楷体";
  text-align: left;
  font-size: 20px;
  text-indent: 30px;
}
</style>
