
<template>
  <div class="login clearfix">
    <div class="login-wrap">
      <el-row type="flex" justify="center">
        <el-form ref="loginForm" :model="user" status-icon label-width="80px">
          <h3>注册</h3>
          <hr />
          <el-form-item prop="username" label="用户名">
            <el-input
              v-model="user.username"
              placeholder="请输入用户名"
            ></el-input>
          </el-form-item>
          <el-form-item prop="email" label="邮箱">
            <el-input v-model="user.email" placeholder="请输入邮箱"></el-input>
          </el-form-item>
          <el-form-item prop="password" label="设置密码">
            <el-input
              v-model="user.password"
              show-password
              placeholder="请输入密码"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" icon @click="registerFn()"
              >注册账号</el-button
            >
          </el-form-item>
        </el-form>
      </el-row>
    </div>
  </div>
</template>
 
<script>
import axios from "axios";
import API_LIST from "@/APIListconfig";

export default {
  name: "login",
  data() {
    return {
      user: {
        username: "",
        email: "",
        password: "",
      },
    };
  },
  created() {
    // console.log($);
    // console.log("1111");
  },
  methods: {
    registerFn() {
      let _registerObj = {
        username: this.user.username,
        email: this.user.email,
        password: this.user.password,
      };
      if (!this.user.username) {
        this.$message.error("请输入用户名！");
        return;
      } else if (!this.user.email) {
        this.$message.error("请输入邮箱！");
        return;
      } else if (this.user.email != null) {
        var reg =
          /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
        if (!reg.test(this.user.email)) {
          this.$message.error("请输入有效的邮箱！");
        } else if (!this.user.password) {
          this.$message.error("请输入密码！");
          return;
        } else {
          axios.post(API_LIST.register_post, _registerObj).then((_d) => {
            console.log(_d.data);
            // 注册成功，返回首页
            if (_d.data.reg_code === 1) {
              this.$alert(_d.data.regInfo, "提示", {
                confirmButtonText: "确定",
                callback: () => {
                  this.$router.push({ path: "/login" });
                },
              });
            } else if (_d.data.reg_code === 2) {
              // 用户名重复，注册失败，清空列表重新输入
              this.$alert(_d.data.regInfo, "提示", {
                confirmButtonText: "确定",
                callback: () => {
                  this.user.username = "";
                  this.user.email = "";
                  this.user.password = "";
                },
              });
            }
          });
        }
      }
    },
  },
};
</script>
 
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login {
  width: 100%;
  height: 740px;
  background: url("../assets/login_bg.jpg") no-repeat;
  background-size: cover;
  overflow: hidden;
}
.login-wrap {
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 20px;
  background-size: cover;
  width: 400px;
  height: 300px;
  margin: 215px auto;
  overflow: hidden;
  padding-top: 20px;
  padding-bottom: 10px;
  line-height: 20px;
}

h3 {
  color: #0babeab8;
  font-size: 24px;
}
hr {
  background-color: #444;
  margin: 20px auto;
}

.el-button {
  width: 60%;
  font-size: 16px;
  margin-left: -68px;
}
</style>
