<template>
  <div>
    <div>
      <header_wrap></header_wrap>
    </div>
    <div class="factors">
      <div class="headertips">
        <el-tag effect="dark"> 冲压条件 </el-tag>
        <el-tag effect="dark" class="el-tag2"> 材料属性 </el-tag>
      </div>
      <el-row :gutter="10">
        <!-- 冲压条件表单 -->
        <el-col :span="10" :offset="1"
          ><div class="grid-content bg-purple">
            <el-form
              :model="ruleForm"
              ref="ruleForm"
              label-width="10px"
              class="demo-ruleForm"
            >
              <!-- 模型名称 -->
              <el-tooltip
                class="item"
                effect="light"
                content="格式：名称.x_t"
                placement="top-start"
              >
                <el-form-item
                  label="导入模型名称"
                  class="upload"
                  label-width="120px"
                >
                  <el-input v-model="ruleForm.Filename"></el-input>

                  <!-- 上传文件 -->

                  <el-upload
                    class="upload-demo"
                    action="http://localhost:5678/uploadModel"
                    :show-file-list="false"
                    :on-success="getUploadSuccess"
                  >
                    <el-button size="small" type="primary">点击上传</el-button>
                  </el-upload>
                </el-form-item>
              </el-tooltip>

              <!-- 工件命名 -->
              <el-tooltip
                class="item"
                effect="light"
                content="以字母开头，3-8个字符"
                placement="top-start"
              >
                <el-form-item label="工件命名" label-width="120px">
                  <el-input v-model="ruleForm.partname"></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 冲压时间 -->
              <el-tooltip
                class="item"
                effect="light"
                content="单位：S"
                placement="top-start"
              >
                <el-form-item label="冲压时间" label-width="120px">
                  <el-input
                    type="number"
                    step="0.1"
                    min="0"
                    v-model.number="ruleForm.time"
                  ></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 冲压距离 -->
              <el-tooltip
                class="item"
                effect="light"
                content="单位：mm"
                placement="top-start"
              >
                <el-form-item label="冲压距离" label-width="120px">
                  <el-input
                    type="number"
                    step="0.1"
                    v-model.number="ruleForm.displacement"
                    autocomplete="off"
                  ></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 项目名称 -->
              <el-tooltip
                class="item"
                effect="light"
                content="以字母开头，3-8个字符"
                placement="top-start"
              >
                <el-form-item label="项目名称" label-width="120px">
                  <el-input v-model="ruleForm.jobname"></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 导入历史数据按钮 -->
              <el-form-item>
                <el-button type="primary" @click="dialogFormVisible = true"
                  >导入历史数据</el-button
                >
              </el-form-item>
            </el-form>

            <!-- 导入历史数据输入框 -->

            <el-dialog
              title="导入历史数据"
              :visible.sync="dialogFormVisible"
              width="30%"
            >
              <el-form :model="importVariable">
                <el-form-item label="项目名称" :label-width="formLabelWidth">
                  <el-input
                    v-model="importVariable.jobname"
                    autocomplete="off"
                  ></el-input>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="importVariableFn"
                  >确 定</el-button
                >
              </div>
            </el-dialog>
          </div></el-col
        >
        <!-- 材料属性表单 -->
        <el-col :span="10" :offset="1"
          ><div class="grid-content bg-purple">
            <el-form
              :model="ruleForm"
              ref="ruleForm"
              label-width="10px"
              class="demo-ruleForm"
            >
              <!-- 模具材料密度 -->
              <el-tooltip
                class="item"
                effect="light"
                content="单位：t/mm³"
                placement="top-start"
              >
                <el-form-item label="模具材料密度" label-width="120px">
                  <el-input
                    type="number"
                    step="0.0000000001"
                    min="0"
                    v-model.number="ruleForm.density1"
                  ></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 工件材料密度 -->
              <el-tooltip
                class="item"
                effect="light"
                content="单位：t/mm³"
                placement="top-start"
              >
                <el-form-item label="工件材料密度" label-width="120px">
                  <el-input
                    type="number"
                    step="0.0000000001"
                    min="0"
                    v-model.number="ruleForm.density2"
                  ></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 模具材料杨氏模量 -->
              <el-tooltip
                class="item"
                effect="light"
                content="单位：N/mm²（即Mpa）"
                placement="top-start"
              >
                <el-form-item
                  label="模具材料杨氏模量"
                  label-width="120px"
                  class="elastic1"
                >
                  <el-input
                    type="number"
                    step="10000"
                    min="0"
                    v-model.number="ruleForm.elastic1"
                  ></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 工件材料杨氏模量 -->
              <el-tooltip
                class="item"
                effect="light"
                content="单位：N/mm²（即Mpa）"
                placement="top-start"
              >
                <el-form-item
                  label="工件材料杨氏模量"
                  label-width="120px"
                  class="elastic1"
                >
                  <el-input
                    type="number"
                    step="10000"
                    min="0"
                    v-model.number="ruleForm.elastic2"
                  ></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 模具材料泊松比 -->
              <el-form-item
                label="模具材料泊松比"
                label-width="120px"
                class="elastic1"
              >
                <el-input
                  type="number"
                  step="0.1"
                  min="0"
                  v-model.number="ruleForm.sdp1"
                ></el-input>
              </el-form-item>

              <!-- 工件材料泊松比 -->
              <el-form-item
                label="工件材料泊松比"
                label-width="120px"
                class="elastic1"
              >
                <el-input
                  type="number"
                  step="0.1"
                  min="0"
                  v-model.number="ruleForm.sdp2"
                ></el-input>
              </el-form-item>

              <!-- ！！！模具材料应力应变 -->
              <el-tooltip
                class="item"
                effect="light"
                content="格式：(xxx,xxx)"
                placement="top-start"
              >
                <el-form-item
                  label="模具材料应力应变"
                  label-width="120px"
                  class="elastic1"
                >
                  <el-input v-model="ruleForm.plasticA"></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- ！！！工件材料应力应变 -->
              <el-tooltip
                class="item"
                effect="light"
                content="格式：(xxx,xxx)"
                placement="top-start"
              >
                <el-form-item
                  label="工件材料应力应变"
                  label-width="120px"
                  class="elastic1"
                >
                  <el-input v-model="ruleForm.plasticB"></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 摩擦系数 -->
              <el-form-item label="摩擦系数" label-width="120px">
                <el-input
                  type="number"
                  step="0.1"
                  min="0"
                  v-model.number="ruleForm.friction"
                ></el-input>
              </el-form-item>

              <!-- 关键点坐标 -->
              <el-tooltip
                class="item"
                effect="light"
                content="格式：(xxx,xxx,xxx)"
                placement="top-start"
              >
                <el-form-item label="关键点坐标" label-width="120px">
                  <el-input v-model="ruleForm.verts"></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 模具网格 -->
              <el-tooltip
                class="item"
                effect="light"
                content="建议在1-3之间选取"
                placement="top-start"
              >
                <el-form-item label="模具网格" label-width="120px">
                  <el-input
                    type="number"
                    step="0.1"
                    min="0"
                    v-model.number="ruleForm.cellsize2"
                  ></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 工件网格 -->
              <el-tooltip
                class="item"
                effect="light"
                content="建议在0.5-2之间选取"
                placement="top-start"
              >
                <el-form-item label="工件网格" label-width="120px">
                  <el-input
                    type="number"
                    step="0.1"
                    min="0"
                    v-model.number="ruleForm.cellsize3"
                  ></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 创建重制按钮 -->
              <el-form-item>
                <el-popconfirm
                  title="确保数据符合要求吗？"
                  @confirm="submitForm(ruleForm)"
                >
                  <el-button type="primary" slot="reference"
                    >提交数据</el-button
                  >
                </el-popconfirm>
              </el-form-item>
            </el-form>
          </div></el-col
        >
      </el-row>
      <el-card shadow="never">
        <el-result icon="warning"> </el-result>
        <h1>注意事项</h1>

        <div class="action">
          <p>1. 确保上传模型正确、所有数据已填入，且数据格式符合提示要求；</p>
          <p>2. 计算开始后请勿关闭平台，以免发生错误；</p>
          <p>3. 计算过程持续大约5-15分钟，请耐心等待完成提示;</p>
          <p>
            4.
            待仿真计算完成，进入结果分析步骤查看仿真结果无误后，即可进入回弹仿真模块。
          </p>
        </div>
        <el-button
          class="start_simulation"
          type="warning"
          @click="start_simulationFn"
          >开始计算</el-button
        >
      </el-card>
    </div>
  </div>
</template>

<script>
import header_wrap from "../../main_wrap/header_wrap/header_wrap.vue";
import axios from "axios";
import API_LIST from "@/APIListconfig";

export default {
  name: "result_page",
  data() {
    return {
      avatar: "",
      Simu_success: "",
      // 上传文件
      fileList: [],
      // 冲压条件表单
      ruleForm: {
        Filename: "",
        partname: "",
        time: "",
        displacement: "",
        jobname: "",
        density1: "",
        density2: "",
        elastic1: "",
        elastic2: "",
        sdp1: "",
        sdp2: "",
        plasticA: "",
        plasticB: "",
        friction: "",
        cellsize2: "",
        cellsize3: "",
        verts: "",
      },
      dialogFormVisible: false,
      importVariable: {
        jobname1: "",
      },
      formLabelWidth: "120px",
    };
  },
  methods: {
    //   冲压条件表单方法
    // 提交数据
    submitForm(formName) {
      axios.post(API_LIST.stampVar_post, formName).then((_d) => {
        if (_d.data.reg_code === 13) {
          this.$message({
            message: "数据提交成功!",
            type: "success",
          });
        } else {
          this.$message({
            message: "数据提交失败！项目名称重复，请重新输入",
            type: "error",
          });
        }
      });
    },

    // 导入历史数据
    importVariableFn() {
      this.dialogFormVisible = false;
      let importVarObj = {
        jobname: this.importVariable.jobname,
      };
      axios.post(API_LIST.importVar_post, importVarObj).then((_d) => {
        // 后端传递过来的_d.data是数组形式，这里需要将其传换成对象形式，以便导入历史的数据自动渲染在页面输入框之内，方便改动
        var arr = _d.data;
        if (arr[0]) {
          let arrObj = {
            Filename: arr[0].Filename,
            partname: arr[0].partname,
            time: arr[0].time,
            displacement: arr[0].displacement,
            jobname: arr[0].jobname,
            density1: arr[0].density1,
            density2: arr[0].density2,
            elastic1: arr[0].elastic1,
            elastic2: arr[0].elastic2,
            sdp1: arr[0].sdp1,
            sdp2: arr[0].sdp2,
            plasticA: arr[0].plasticA,
            plasticB: arr[0].plasticB,
            friction: arr[0].friction,
            verts: arr[0].verts,
            cellsize2: arr[0].cellsize2,
            cellsize3: arr[0].cellsize3,
          };
          this.ruleForm = arrObj;
          this.$message({
            type: "success",
            message: "数据导入成功!",
          });
        } else {
          this.$message({
            type: "error",
            message: "项目名不存在，导入历史数据失败!",
          });
        }
      });
    },

    // 开始计算
    start_simulationFn() {
      this.$confirm("计算开始将不能撤销，请仔细检查数据，是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          // 提交计算接口
          axios.post(API_LIST.startStamp_post, this.ruleForm).then((_d) => {
            // 计算成功返回
            this.Simu_success = _d.data.regInfo;
            this.$alert(this.Simu_success, "Simulation_Platform", {
              confirmButtonText: "确定",
              callback: () => {
                // 点击确认按钮后跳转到结果分析页，改变侧边栏状态、传值、刷新页面
                sessionStorage.setItem("active_inx", "/result_page");
                this.$router.push({
                  path: "/result_page",
                });
                // 保存项目名到会话存储中
                sessionStorage.setItem("jobname", this.ruleForm.jobname);
                // 刷新页面（侧边栏）
                location.reload();
              },
            });
          });

          // 点击确认提交按钮
          this.$message({
            type: "success",
            message: "提交成功，开始计算!",
          });
        })
        .catch(() => {
          location.reload();
          this.$message({
            type: "info",
            message: "提交失败，请重试!",
          });
        });
    },
    getUploadSuccess(res, file) {
      // this.avatar = res.avatar;
      // console.log(564);
      this.$message({
        type: "success",
        message: "模型上传成功!",
      });
    },
  },
  components: { header_wrap },
};
</script>

