<template>
  <div>
    <div>
      <header_wrap></header_wrap>
    </div>
    <div class="factors">
      <div class="headertips">
        <el-tag effect="dark"> 回弹数据 </el-tag>
      </div>
      <el-row :gutter="10">
        <!-- 回弹表单 -->
        <el-col :span="10" :offset="1"
          ><div class="grid-content bg-purple">
            <el-form
              :model="ruleForm1"
              ref="ruleForm1"
              label-width="10px"
              class="demo-ruleForm"
            >
              <!-- 模型名称 -->
              <el-tooltip
                class="item"
                effect="light"
                content="与冲压仿真项目名称相同"
                placement="top-start"
              >
                <el-form-item
                  label="导入模型名称"
                  class="upload"
                  label-width="120px"
                >
                  <el-input v-model="ruleForm1.jobname"></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 回弹项目名称 -->
              <el-tooltip
                class="item"
                effect="light"
                content="以字母开头，3-8个字符"
                placement="top-start"
              >
                <el-form-item label="回弹项目名称" label-width="120px">
                  <el-input v-model="ruleForm1.jobname1"></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 固定线坐标a -->
              <el-tooltip
                class="item"
                effect="light"
                content="格式：(xxx,xxx,xxx)"
                placement="top-start"
              >
                <el-form-item label="固定线坐标a" label-width="120px">
                  <el-input v-model="ruleForm1.edges1"></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 固定线坐标b -->
              <el-tooltip
                class="item"
                effect="light"
                content="格式：(xxx,xxx,xxx)"
                placement="top-start"
              >
                <el-form-item label="固定线坐标b" label-width="120px">
                  <el-input v-model="ruleForm1.edges2"></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 固定线坐标c -->
              <el-tooltip
                class="item"
                effect="light"
                content="格式：(xxx,xxx,xxx)"
                placement="top-start"
              >
                <el-form-item label="固定线坐标c" label-width="120px">
                  <el-input v-model="ruleForm1.edges3"></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 固定点坐标1 -->
              <el-tooltip
                class="item"
                effect="light"
                content="格式：(xxx,xxx,xxx)"
                placement="top-start"
              >
                <el-form-item label="固定点坐标1" label-width="120px">
                  <el-input v-model="ruleForm1.verts1"></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 固定点坐标2 -->
              <el-tooltip
                class="item"
                effect="light"
                content="格式：(xxx,xxx,xxx)"
                placement="top-start"
              >
                <el-form-item label="固定点坐标2" label-width="120px">
                  <el-input v-model="ruleForm1.verts2"></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 固定点坐标3 -->
              <el-tooltip
                class="item"
                effect="light"
                content="格式：(xxx,xxx,xxx)"
                placement="top-start"
              >
                <el-form-item label="固定点坐标3" label-width="120px">
                  <el-input v-model="ruleForm1.verts3"></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 固定点坐标4 -->
              <el-tooltip
                class="item"
                effect="light"
                content="格式：(xxx,xxx,xxx)"
                placement="top-start"
              >
                <el-form-item label="固定点坐标4" label-width="120px">
                  <el-input v-model="ruleForm1.verts4"></el-input>
                </el-form-item>
              </el-tooltip>

              <!-- 是否保压 -->
              <el-form-item label="是否保压" label-width="120px">
                <el-switch
                  v-model="hold"
                  active-color="#13ce66"
                  inactive-color="#ff4949"
                >
                </el-switch>
              </el-form-item>

              <!-- 提交数据按钮 -->
              <el-form-item>
                <el-popconfirm
                  title="确保数据符合要求吗？"
                  @confirm="submitForm1(ruleForm1)"
                >
                  <el-button type="primary" slot="reference"
                    >提交数据</el-button
                  >
                </el-popconfirm>

                <!-- 导入历史数据按钮 -->
                <el-button type="primary" @click="dialogFormVisible = true"
                  >导入历史数据</el-button
                >
              </el-form-item>
            </el-form>

            <!-- 导入历史数据输入框 -->
            <el-dialog
              title="导入历史数据"
              :visible.sync="dialogFormVisible"
              width="30%"
            >
              <el-form :model="importVariable">
                <el-form-item
                  label="回弹项目名称"
                  :label-width="formLabelWidth"
                >
                  <el-input
                    v-model="importVariable.jobname1"
                    autocomplete="off"
                  ></el-input>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="importVariableFn"
                  >确 定</el-button
                >
              </div>
            </el-dialog>
          </div></el-col
        >
        <el-col :span="10" :offset="1">
          <transition name="el-zoom-in-left">
            <div class="grid-content bg-purple hold" v-show="hold">
              <div class="headertips1">
                <el-tag effect="dark" class="el-tag2"> 保压参数 </el-tag>
              </div>
              <el-form
                :model="ruleForm1"
                ref="ruleForm1"
                label-width="10px"
                class="demo-ruleForm"
              >
                <!-- 保压时间 -->
                <el-tooltip
                  class="item"
                  effect="light"
                  content="单位：S"
                  placement="top-start"
                >
                  <el-form-item label="保压时间" label-width="120px">
                    <el-input
                      type="number"
                      step="0.1"
                      min="0"
                      v-model.number="ruleForm1.holdtime"
                    ></el-input>
                  </el-form-item>
                </el-tooltip>

                <!-- 冲压距离 -->
                <el-tooltip
                  class="item"
                  effect="light"
                  content="单位：mm"
                  placement="top-start"
                >
                  <el-form-item label="冲压距离" label-width="120px">
                    <el-input
                      type="number"
                      step="0.1"
                      v-model.number="ruleForm1.displacement"
                      autocomplete="off"
                    ></el-input>
                  </el-form-item>
                </el-tooltip>

                <!-- 上模参考点 -->
                <el-tooltip
                  class="item"
                  effect="light"
                  content="格式：(xxx,xxx,xxx)"
                  placement="top-start"
                >
                  <el-form-item label="模型参考点坐标" label-width="120px">
                    <el-input v-model="ruleForm1.repoint"></el-input>
                  </el-form-item>
                </el-tooltip>
              </el-form>
            </div>
          </transition>
        </el-col>
      </el-row>

      <!-- 注意事项 -->
      <el-card shadow="never">
        <el-result icon="warning"> </el-result>
        <h1>注意事项</h1>

        <div class="action">
          <p>1. 确保所有数据填入，且满足提示要求；</p>
          <p>2. 仿真计算开始后请勿关闭平台，以免计算发生错误；</p>
          <p>
            3.
            平台采用显/隐式分析联合的方法，大大缩短计算时间，请耐心等待完成提示;
          </p>
          <p>
            4.
            待仿真计算完成，进入结果分析步骤查看仿真结果，检查结果无误后可选择保存结果。
          </p>
        </div>
        <el-button
          class="start_simulation"
          type="warning"
          @click="start_simulationFn1"
          >开始计算</el-button
        >
      </el-card>
    </div>
  </div>
</template>

<script>
import header_wrap from "../../main_wrap/header_wrap/header_wrap.vue";
import axios from "axios";
import API_LIST from "@/APIListconfig";

export default {
  name: "result_page",
  data() {
    return {
      // 冲压条件表单
      ruleForm1: {
        jobname: "",
        jobname1: "",
        edges1: "",
        edges2: "",
        edges3: "",
        verts1: "",
        verts2: "",
        verts3: "",
        verts4: "",
        holdtime: 0,
        displacement: 0,
        repoint: 0,
      },
      Simu_success1: "",
      // 导入历史数据
      dialogFormVisible: false,
      importVariable: {
        jobname1: "",
      },
      formLabelWidth: "120px",

      // 是否保压
      hold: false,
    };
  },
  methods: {
    // 提交数据
    submitForm1(formName) {
      axios.post(API_LIST.springBackVar_post, formName).then((_d) => {
        if (_d.data.reg_code === 14) {
          this.$message({
            message: "数据提交成功!",
            type: "success",
          });
        }
      });
    },

    // 导入历史数据
    importVariableFn() {
      this.dialogFormVisible = false;
      let importVarObj = {
        jobname1: this.importVariable.jobname1,
      };
      axios.post(API_LIST.importBackVar_post, importVarObj).then((_d) => {
        // 后端传递过来的_d.data是数组形式，这里需要将其传换成对象形式，以便导入历史的数据自动渲染在页面输入框之内，方便改动
        var arr = _d.data;
        if (arr[0]) {
          // console.log(arr);
          let arrObj = {
            jobname: arr[0].jobname,
            jobname1: arr[0].jobname1,
            edges1: arr[0].edges1,
            edges2: arr[0].edges2,
            edges3: arr[0].edges3,
            verts1: arr[0].verts1,
            verts2: arr[0].verts2,
            verts3: arr[0].verts3,
            verts4: arr[0].verts4,
            holdtime: arr[0].holdtime,
            displacement: arr[0].displacement,
            repoint: arr[0].repoint,
          };
          this.ruleForm1 = arrObj;
          this.$message({
            type: "success",
            message: "导入历史数据成功!",
          });
        } else {
          // console.log(123);
          this.$message({
            type: "error",
            message: "项目名不存在，导入历史数据失败!",
          });
        }
      });
    },

    // 开始计算
    start_simulationFn1() {
      this.$confirm("计算开始将不能撤销，请仔细检查数据，是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          // 提交计算接口
          console.log(132);
          axios
            .post(API_LIST.startSpringback_post, this.ruleForm1)
            .then((_d) => {
              console.log(123);
              // 计算成功返回
              this.Simu_success1 = _d.data.regInfo;
              this.$alert(this.Simu_success1, "Simulation_Platform", {
                confirmButtonText: "确定",
                callback: () => {
                  // 点击确认按钮后跳转到结果分析页，改变侧边栏状态、传值、刷新页面
                  sessionStorage.setItem("active_inx", "/result_page");
                  this.$router.push({
                    path: "/result_page",
                  });
                  // sessionStorage.setItem("jobname", this.ruleForm1.jobname);
                  location.reload();
                },
              });
            });
          // 点击确认提交按钮
          this.$message({
            type: "success",
            message: "提交成功，开始计算!",
          });
        })
        .catch(() => {
          console.log("提交失败，请重试!");
          this.$message({
            type: "info",
            message: "提交失败，请重试!",
          });
        });
    },
  },
  components: { header_wrap },
};
</script>

