<template>
  <div class="table">
    <el-row :gutter="20">
      <el-col :span="16" :offset="2">
        <div class="grid-content bg-purple">
          <!-- 数据表格 -->
          <el-table :data="userInfo" stripe style="width: 100%">
            <el-table-column prop="username" label="用户名" width="180">
            </el-table-column>
            <el-table-column prop="password" label="密码" width="180">
            </el-table-column>
            <el-table-column prop="email" label="邮箱" width="220">
            </el-table-column>
            <el-table-column label="操作" width="170">
              <template slot-scope="scope">
                <el-button @click="dialogFormVisible = true" size="mini"
                  >编辑</el-button
                >
                <el-button
                  type="danger"
                  size="mini"
                  @click="deleteUserInfoFn(scope.$index, scope.row)"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </div></el-col
      >
    </el-row>

    <!-- 编辑弹窗 -->
    <el-dialog title="用户信息" :visible.sync="dialogFormVisible">
      <el-form :model="user">
        <el-form-item label="用户名" :label-width="formLabelWidth">
          <el-input v-model="user.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" :label-width="formLabelWidth">
          <el-input v-model="user.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" :label-width="formLabelWidth">
          <el-input v-model="user.email" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="changeUserInfoFn">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import API_LIST from "@/APIListconfig";

export default {
  data() {
    return {
      // 编辑表单数据
      user: {
        username: "",
        password: "",
        email: "",
      },
      // 弹窗表单显示
      dialogFormVisible: false,
      // 弹窗宽度
      formLabelWidth: "80px",
      // 用来接受用户数据
      userInfo: [],
    };
  },
  created() {
    this.getUserInfo();
  },
  methods: {
    // 获取用户信息
    getUserInfo() {
      axios.get(API_LIST.userInfo).then((_d) => {
        this.userInfo = _d.data;
      });
    },
    // 编辑弹窗确认事件
    changeUserInfoFn() {
      let _userInfoObj = {
        username: this.user.username,
        password: this.user.password,
        email: this.user.email,
      };
      this.dialogFormVisible = false;

      // 编辑数据库中的数据
      axios.post(API_LIST.userInfo_post, _userInfoObj).then((_d) => {
        // 再次调用获取数据函数，刷新页面（接口嵌套）
        this.getUserInfo();
      });
    },

    // 删除按钮事件
    deleteUserInfoFn(index, row) {
      var deleteUserInfoObj = this.userInfo[index];
      // 删除数据库中的数据
      axios
        .post(API_LIST.deleteUserInfo_post, deleteUserInfoObj)
        .then((_d) => {});
      // 删除后重新调接口读取用户数据(内部不能执行？)
      this.getUserInfo();
    },
  },
};
</script>

<style scoped>
.table {
  margin-top: 30px;
}
/* 弹窗 */
.table /deep/ .el-dialog {
  width: 400px;
}
.table /deep/ .el-dialog__body {
  padding: 15px 10px;
}
.dialog-footer .el-button {
  margin-top: -50px;
}

/* 表格标题居中 */
.el-table /deep/ .cell {
  text-align: center;
}
</style>