<template>
  <div class="login" clearfix>
    <div class="login-wrap">
      <el-row type="flex" justify="center">
        <el-form ref="loginForm" :model="user" status-icon label-width="80px">
          <h3>登录</h3>
          <hr />
          <el-form-item prop="username" label="用户名">
            <el-input
              v-model="user.username"
              placeholder="请输入用户名"
              prefix-icon
            ></el-input>
          </el-form-item>
          <el-form-item id="password" prop="password" label="密码">
            <el-input
              v-model="user.password"
              show-password
              placeholder="请输入密码"
            ></el-input>
          </el-form-item>
          <router-link to="/register" class="register">注册账号</router-link>
          <el-form-item>
            <el-button type="primary" @click="loginFn()">登 录</el-button>
          </el-form-item>
        </el-form>
      </el-row>
    </div>
  </div>
</template>
 
<script>
import axios from "axios";
import API_LIST from "@/APIListconfig";

export default {
  name: "login",
  data() {
    return {
      user: {
        username: "",
        password: "",
      },
    };
  },
  created() {},
  methods: {
    loginFn() {
      let _loginObj = {
        u: this.user.username,
        p: this.user.password,
      };

      if (!this.user.username) {
        this.$message.error("请输入用户名！");
        return;
      } else if (!this.user.password) {
        this.$message.error("请输入密码！");
        return;
      } else {
        axios.post(API_LIST.userLogin_post, _loginObj).then((res) => {
          if (res.data.reg_code === 3) {
            this.$message.success(
              " 欢迎回来 " +
                _loginObj.u +
                "，  为获得最佳阅读体验，请将浏览器缩放比例调整为125%"
            );
            this.$router.push({ path: "/instruction_page" });
            sessionStorage.setItem("active_inx", "/instruction_page");
            return;
          } else if (res.data.reg_code === 6) {
            this.$message.success(
              " 欢迎回来 " + _loginObj.u + "，  管理员登录成功！"
            );
            this.$router.push({ path: "/management" });
            sessionStorage.setItem("active_inx", "/user_info");
            return;
          } else if (res.data.reg_code === 4) {
            this.$message.error(res.data.regInfo);
            return;
          } else if (res.data.reg_code === 5) {
            this.$message.error(res.data.regInfo);
            return;
          }
        });
      }
    },
  },
};
</script>
 
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login {
  width: 100%;
  height: 740px;
  background: url("../assets/login_bg.jpg") no-repeat;
  background-size: cover;
  overflow: hidden;
}
.login-wrap {
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 20px;
  background-size: cover;
  width: 400px;
  height: 300px;
  margin: 215px auto;
  overflow: hidden;
  padding-top: 10px;
  line-height: 40px;
}

#password {
  margin-bottom: 5px;
}
h3 {
  color: #0babeab8;
  font-size: 24px;
}
hr {
  background-color: #444;
  margin: 20px auto;
}
a {
  text-decoration: none;
  color: #aaa;
  font-size: 15px;
}
a:hover {
  color: coral;
}
.register {
  margin-left: 8px;
  color: #444;
}
.el-button {
  width: 60%;
  font-size: 16px;
  margin-left: -68px;
}
</style>