<script>
import axios from "axios";
import API_LIST from "@/APIListconfig";

export default {
  data() {
    return {
      // 用来接受冲压仿真数据
      variable_stamp: [],
      // 保存选中的详细数据
      selectStampDetail: [],
      dialogFormVisible: false,
      dialogFormVisible1: false,
      formLabelWidth: "130px",
      // 用来接受回弹仿真数据
      variable_springback: [],
      // 保存选中的详细数据
      selectSpringBackDetail: [],
    };
  },
  created() {
    // 获取冲压仿真变量数据信息
    this.getStampVarFn();

    //获取回弹仿真变量数据信息
    this.getSpringBackVarFn();
  },
  methods: {
    // 获取冲压仿真变量数据信息
    getStampVarFn() {
      axios.get(API_LIST.stampVar).then((_d) => {
        this.variable_stamp = _d.data;
      });
    },

    // 删除冲压仿真变量数据
    delStampVarFn(index, row) {
      var deleteVarObj = this.variable_stamp[index];
      // 删除数据库中的数据
      axios.post(API_LIST.delstampVar, deleteVarObj).then((_d) => {});
      // 删除后重新调接口读取用户数据(内部不能执行？)
      this.getStampVarFn();
    },

    // 冲压仿真变量详细数据
    detailStampVarFn(index, row) {
      var detailStampVar = this.variable_stamp[index];
      this.selectStampDetail = detailStampVar;
      console.log(this.selectStampDetail);
      this.dialogFormVisible = true;
    },

    // 获取回弹仿真变量数据信息
    getSpringBackVarFn() {
      axios.get(API_LIST.springBackVar).then((_d) => {
        this.variable_springback = _d.data;
      });
    },

    // 删除回弹仿真变量数据
    deleteSpringBackVarFn(index, row) {
      var deleteVarObj = this.variable_springback[index];
      // 删除数据库中的数据
      axios.post(API_LIST.delspringbackVar, deleteVarObj).then((_d) => {});
      // 删除后重新调接口读取用户数据(内部不能执行？)
      this.getSpringBackVarFn();
    },

    // 回弹仿真变量详细数据
    detailSpringBackVarFn(index, row) {
      var detailSpringBackVar = this.variable_springback[index];
      this.selectSpringBackDetail = detailSpringBackVar;
      console.log(this.selectSpringBackDetail);
      this.dialogFormVisible1 = true;
    },
  },
};
</script>

<style scoped>
.title p {
  font-size: 26px;
  float: left;
  margin-left: 140px;
  margin-right: 130px;
  margin-bottom: 25px;
}

.dialog-footer {
  text-align: center;
  margin-top: -30px;
}

/* 详细数据框大小 */
.dialog_width /deep/ .el-dialog {
  width: 66% !important;
}

/* 表格标题居中 */
.el-table /deep/ .cell {
  text-align: center;
}
</style>